<template>
	<div id="meIndex">
		<div id="me">
			<van-image id="headimg" round width="8rem" height="8rem" :src=headimgurl fit="cover"></van-image>
			<p>{{ nickname }}</p>
		</div>
		<div id="myImg">
			<van-collapse v-model="activeNames" @change="query" >
				<van-collapse-item title="我上传的" name="myImg" >
					<van-list v-model="loading" :finished="finished"
					:error.sync="error" error-text="请求失败，点击重新加载" @load="onLoad" id="myImgList">
						<van-card v-for="(item, index) in imglist" :key="index" :thumb="item.thumburl" 
						:title="item.imgname" :desc="item.uploadtime" :tag="item.liked" @click-thumb="imgPreview(index)" >
							<div slot="footer">
								<van-button size="mini" @click="deleteImg(item.filehash)" >删除</van-button>
							</div>
						</van-card>
						<p>{{emptyText}}</p>
					</van-list>
				</van-collapse-item>
			</van-collapse>
		</div>
	</div>
</template>

<script>
	import {
		Button,
		Panel,
		Image,
		Tab, Tabs,
		Collapse, CollapseItem,
		List, Card,
		ImagePreview,
		Toast
	} from 'vant';

	import axios from 'axios';

	export default {
		components: {
			[Button.name]: Button,
			[Panel.name]: Panel,
			[Image.name]: Image,
			[Tab.name]: Tab, [Tabs.name]: Tabs,
			[Collapse.name]: Collapse, [CollapseItem.name]: CollapseItem,
			[List.name]: List, [Card.name]: Card,
			[ImagePreview.name]: ImagePreview,
			[Toast.name]: Toast
		},
		data() {
			return {
				activeNames: [],
				imglist: [],
				previewlist: [],
				error: false,
				loading: false,
				emptyText: null,
				finished: false
			}
		},
		computed: {
			nickname: function() {
				return this.$store.state.nickname
			},
			headimgurl: function() {
				return this.$store.state.headimgurl
			}
		},
		methods: {
			query() {
				
			},
			imgPreview(index) {
				ImagePreview({
					images: this.previewlist,
					startPosition: index
				})
			},
			deleteImg(target) {
				let path = this.$store.state.serverurl + '/api/delete'
				axios.get(path, {
					params: {
						filehash: target
					}
				})
				.then(response => {
					if (response.data.deleted == 'true'){
						Toast.success('已删除')
						this.loading = true
						this.imglist = []
						this.onLoad()
					}
					else {
						Toast.fail('删除失败')
					}
				})
			},
			onLoad() {
				if(this.activeNames == 'myImg'){
					
					let path = this.$store.state.serverurl + '/api/myimg'
					axios.get(path, {
						params: {
							openid: this.$store.state.openid
						}
					})
					.then(response => {
						//console.log(response)
						if (response.data.empty == 'true'){
							this.emptyText = '你还没上传过呢'
						}
						else {
							this.emptyText = null
							for (let i in response.data) {
								let obj = {};
								obj['filehash'] = response.data[i].filehash
								obj['thumburl'] = this.$store.state.serverurl + '/thumbnail/' + response.data[i].filehash
								obj['imgurl'] = this.$store.state.serverurl + '/image/' + response.data[i].filehash
								this.previewlist[i] = this.$store.state.serverurl + '/image/' + response.data[i].filehash
								obj['imgname'] = response.data[i].imgname
								obj['uploadtime'] = response.data[i].uploadtime
								obj['liked'] = response.data[i].liked.toString()
								this.imglist.push(obj)
							}
						}
						this.loading = false
						this.finished = true
					})
					.catch(() => {
						this.error = true
						this.loading = false
					})
				}
			}
		}
	}
</script>

<style>
	html, body{
		height: 100%;
		margin: 0;
		padding: 0;
	}
	#meIndex {
		background-image: url(../assets/bg.jpg);
		height: 100%;	
	}
	#me {
		text-align: center;
		margin-bottom: 5%;
	}
	#headimg {
		margin-top: 2rem;
	}
	#myImgList {
		margin-bottom: 30px;
	}
</style>
